"use client";

import { useState } from "react";
import FavoriteIcon from "@mui/icons-material/Favorite";
import FavoriteBorderOutlinedIcon from "@mui/icons-material/FavoriteBorderOutlined";

type Props = {
  petId: string;
  initiallyFav: boolean;
  onUnfavorited?: (petId: string) => void;
  onFavoriteRestored?: (petId: string) => void;
};

export default function FavoriteButton({
  petId,
  initiallyFav,
  onUnfavorited,
  onFavoriteRestored,
}: Props) {
  const [isFavorite, setIsFavorite] = useState(initiallyFav);
  const [isLoading, setIsLoading] = useState(false);

  const handleClick = async (e: React.MouseEvent) => {
    e.preventDefault(); // Prevent Link navigation
    e.stopPropagation(); // Prevent event bubbling

    console.log("üî• FavoriteButton clicked", {
      petId,
      isFavorite,
      onUnfavorited: !!onUnfavorited,
    });

    setIsLoading(true);
    const newFavoriteState = !isFavorite;
    const previousFavoriteState = isFavorite;

    // Optimistic update - update UI immediately
    setIsFavorite(newFavoriteState);
    console.log("üîÑ Set favorite state to:", newFavoriteState);

    // If unfavoriting, remove from list immediately
    if (!newFavoriteState && onUnfavorited) {
      console.log("‚ùå Calling onUnfavorited for pet:", petId);
      onUnfavorited(petId);
    } else if (!newFavoriteState) {
      console.log(
        "‚ö†Ô∏è onUnfavorited callback is missing - pet won't be removed from list"
      );
    }

    try {
      console.log("üåê Making API call...");
      const response = await fetch("/api/favorites", {
        method: newFavoriteState ? "POST" : "DELETE",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ petId }),
      });

      if (!response.ok) {
        throw new Error(`API request failed: ${response.status}`);
      }

      console.log("‚úÖ API call successful");
    } catch (error) {
      console.error("‚ùå Failed to update favorite:", error);

      // Rollback on error
      setIsFavorite(previousFavoriteState);
      console.log("üîÑ Rolled back favorite state to:", previousFavoriteState);

      // If we optimistically removed it and API failed, restore it
      if (!newFavoriteState && onFavoriteRestored) {
        console.log("üîÑ Calling onFavoriteRestored for pet:", petId);
        onFavoriteRestored(petId);
      }
    } finally {
      setIsLoading(false);
      console.log("üèÅ Finished processing favorite toggle");
    }
  };

  return (
    <button
      onClick={handleClick}
      disabled={isLoading}
      className={`
        p-2 rounded-full transition-all duration-200 z-10 relative
        ${isLoading ? "opacity-50 cursor-not-allowed" : "hover:scale-105"}
        ${!isLoading ? "hover:bg-orange-200" : ""}
      `}
      style={{
        color: "#ed9426",
        backgroundColor: isFavorite
          ? "rgba(237, 148, 38, 0.15)"
          : "transparent",
      }}
      onMouseEnter={(e) => {
        if (!isLoading) {
          e.currentTarget.style.backgroundColor = "rgba(237, 148, 38, 0.3)";
        }
      }}
      onMouseLeave={(e) => {
        if (!isLoading) {
          e.currentTarget.style.backgroundColor = isFavorite
            ? "rgba(237, 148, 38, 0.15)"
            : "transparent";
        }
      }}
      aria-label={isFavorite ? "Remove from favorites" : "Add to favorites"}
    >
      {isFavorite ? <FavoriteIcon /> : <FavoriteBorderOutlinedIcon />}
    </button>
  );
}
